<template>
  <div class="share">

    <div class="menmuTitle flex align between">
        <h1>推荐分享</h1>
        <div class="goLink pointer hoverTr3" @click="gopage('/userCenter/userShareList')">分享记录&nbsp;&nbsp;<i class="el-icon-right"></i></div>
      </div>
      <!-- menmuTitle -->


    <div class="mainBox flex align">

      <div class="codeBox">
        <div class="qrcode" ref="qrCodeUrl" id="qrCodeUrl"></div>
      </div>

      <div class="text">
        <h2>我的邀请码：<b style="font-size:33px;">{{userInfo.user.userInvitation}}</b></h2>
        <div class="trx">{{code_text}}</div>
        <div class="copyBtn copycode"  :data-clipboard-text="code_text" @click="copy">复制链接</div>
      </div>
      <!-- text -->

    </div>
    <!-- mainBox -->

    <div class="info">
      <div class="red"><i class="el-icon-warning"></i>&nbsp;&nbsp;<span>分享给好友，可获得额外奖励！</span></div>
      <!-- <div>注册SUTER钱包后，尽享惊喜。 从现在开始，所有使用推荐链接在SUTER钱包上完成注册的用户都将获得500美元的配额，以95％的现货价格折扣体验套利交易。 如果被邀请者完成的USDT至SUTER互换超过50 USDT，则邀请者将获得500 USDT互换配额和被邀请者首次互换的5％互换数量奖励。 奖金的有效期是从现在到2020年6月30日。</div> -->
    </div>
    <!-- info -->

  </div>
</template>

<script>
  import {
    mapMutations,
    mapState
  } from 'vuex'

  import QRCode from 'qrcodejs2'
  import Clipboard from 'clipboard';


  

  export default {
    props: ['dataGroup'],
    data() {
      return {
         code_text:''
      }
    },
    components: {

    },
    computed: {
      ...mapState(['userInfo']),
    },
    methods: {

      async qrcode(address) { //生成二维码
        this.$refs.qrCodeUrl.innerHTML="";//先移除
        var qrcode = new QRCode(this.$refs.qrCodeUrl, {
          text: address,
          colorDark : "#2b3b4b",
          width: 150,
          height: 150,
          correctLevel: QRCode.CorrectLevel.H
        })
      }, //qrcode

      copy() { //复制
        var clipboard = new Clipboard('.copycode')
        clipboard.on('success', e => {
          this.$message.success(this.$t('wallet.common.toast_copy_ok'))
          clipboard.destroy()
        })
        clipboard.on('error', e => {
          this.$message.error(this.$t('wallet.common.toast_copy_fail'))
          clipboard.destroy()
        })
      }//copy

    },
    mounted() {
      this.code_text = window.location.protocol+'//'+window.location.host+'/#/register?invitation='+this.userInfo.user.userInvitation;
      setTimeout(() => {
        this.qrcode(this.code_text)
      }, 300);
    }
  };

</script>
<style rel="stylesheet/scss" scoped lang="scss">
  @import "../styles/Val";
  .share{
    padding: 30px 0;
    .mainBox {
      padding: 50px 0;
      .codeBox{
        margin-right: 60px;
      }
      h2{
        font-size: 20px;
        font-weight: bold;
        margin-bottom: 30px;
      }
      .trx{
        font-size: 18px;
        color: $them_color;
      }
      .copyBtn{
        border: 1px solid $them_color;
        color: $them_color;
        display: inline-block;
        padding: 5px 20px;
        border-radius: 2px;
        margin-top: 10px;
        cursor: pointer;
        transition: all 0.3s;
        -webkit-transition: all 0.3s;
        &:hover{
          background: $them_color;
          color: white;
        }
      }
    }
    .info{
      .red{
        color: #F15772;
        margin-bottom: 15px;
      }
      color: $them_color_gray_light;
      line-height: 1.8em;
    }
  }
</style>



